import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { VendorService } from '../../../.././api/services/vendor.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-expense-view-invoice-history',
  templateUrl: './expense-view-invoice-history.component.html',
  styleUrls: ['./expense-view-invoice-history.component.scss']
})
export class ExpenseViewInvoiceHistoryComponent implements OnInit {

  isInvoiceDataLoaded:boolean = false;

  invoiceData:string = "";
  invoiceDataList:any;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "vendorInvoiceNo";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  accountUserDetails:any;

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  selectedInput:string = "";
  columnField: any = {}

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private vendorService:VendorService,
  	private accountsService:AccountsService,
    private lookupService: LookupService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getUserName(id){

  	var data = _.filter( this.userDataList, function( item ) {
      if(item.userId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName+' '+data[0].lastName;
    }

  }

  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getInvoiceDate(invoice, date){

    this.invoiceDataList.map(obj=> {
      if(obj.vendorInvoiceNo == invoice.vendorInvoiceNo){
        obj.invoiceDateFormat = moment(date).format("DD/MM/YYYY");
      }
   });

  	return moment(date).format("DD/MM/YYYY");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

   this.vendorService.getVendorByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.vendorId == this.route.params['value'].id;
      });

      

      this.isVendorDataLoaded = true;

      this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

        this.vendorCategoryData = res.filter(item => {
            return item.isActive;
        });

      });


  });

  	var accountParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      groupId: 3
    }

    this.accountsService.getGLAccountsByGroupId(accountParams).subscribe((res:any) => {

      this.glAccountListData = res;

    });


  	this.accountsService.GetVendorPaymenntHistoryById(this.route.params['value'].id).subscribe((res:any) => {


  		this.invoiceDataList = res;

  		this.totalItems = this.invoiceDataList.length;

  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
		  }

      this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

        this.userDataList = res;
        this.isInvoiceDataLoaded = true;

      }, error => {
        
      });


  		

  	})


  }

}
