import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { VendorService } from '../../../.././api/services/vendor.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { UserService } from '../../../../api/services/user.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-expense-post-invoice',
  templateUrl: './expense-post-invoice.component.html',
  styleUrls: ['./expense-post-invoice.component.scss']
})
export class ExpensePostInvoiceComponent implements OnInit {

  invoice:any;
  invoiceId:string = "";

  isEditInvoice:boolean = false;
  isNewInvoice:boolean = false;

  vendorTypeDataList:any;
  glAccountListData:any;

  isInvoiceSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  vendorDataList:any;
  isVendorDataLoaded:boolean = false;
  vendorCategoryData:any;

  glAccountIndicator:any = "Expenses";

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private vendorService:VendorService,
    private lookupService: LookupService,
    private accountsService:AccountsService,
    private userService:UserService,
    private apartmentService:ApartmentService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }


  getCategoryName(id){
    
    var data = _.filter( this.vendorCategoryData, function( item ) {
      if(item.lookupValueId === parseInt(id))
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName;
    }

  }

  submitExpenseInvoiceForm(form){

  	this.isInvoiceSubmitted = false;

    if(!this.isEditInvoice) {

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "vendorId": parseInt(this.invoice.vendorId),
        "vendorInvoiceNumber": this.invoice.vendorInvoiceNumber,
        "vendorInvoiceAmount": this.invoice.vendorInvoiceAmount,
        "vendorInvoiceDate": new Date().toISOString(),
        "dueDate": this.invoice.vendorInvoiceDate,
        "stateGst": 0,
        "centralGst": 0,
        "integratedGst": 0,
        "isDeductTds": true,
        "deductTds": 0,
        "deductTdstypeId": 1,
        "expenseHeadId": 1,
        "payeeId": 1,
        "isEmailSent": true,
        "isSmssent": true,
        "voucherNumber": null,
        "vendorInvoiceStatusId": null,
        "postedBy": parseInt(this.cookieService.get('userId')),
        "postedOn": new Date().toISOString(),
        "comments": this.invoice.comments || "",
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString(),
        "transReference1": "string",
        "transReference2": "string",
        "vendorInvoiceGlaccount": [
          {
            "apartmentId": parseInt(this.cookieService.get('apartmentId')),
            "invoiceId": 0,
            "glaccountId": parseInt(this.invoice.glaccountId),
            "comments": this.invoice.comments || "",
            "isActive": true,
            "insertedBy": parseInt(this.cookieService.get('userId')),
            "insertedOn": new Date().toISOString(),
            "updatedBy": null,
            "updatedOn": new Date().toISOString(),
            "tax1": 0,
            "tax2": 0,
            "tax3": 0,
            "deductTax": 0,
            "amount": this.invoice.vendorInvoiceAmount
          }
        ]

    }

    this.accountsService.addVendorInvoice(details).subscribe((res:any) => {

      if(res.message){

      this.isInvoiceSubmitted = true;
      this.sharedService.setAlertMessage("Expense added successfully");
      this.router.navigate(['ams/expense/subledger']);

        }
        else {

      this.isInvoiceSubmitted = true;
      this.isError = true;
      this.alertMessage = res.errorMessage;
         
        }

    },
    error => {

      this.isInvoiceSubmitted = true;

    });


    }

    else {

      let details = {

        "vendorInvoiceId": this.invoice.vendorInvoiceId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "vendorId": parseInt(this.invoice.vendorId),
        "vendorInvoiceNumber": this.invoice.vendorInvoiceNumber,
        "vendorInvoiceAmount": this.invoice.vendorInvoiceAmount,
        "vendorInvoiceDate": new Date().toISOString(),
        "dueDate": this.invoice.vendorInvoiceDate,
        "stateGst": 0,
        "centralGst": 0,
        "integratedGst": 0,
        "isDeductTds": true,
        "deductTds": 0,
        "deductTdstypeId": 1,
        "expenseHeadId": 1,
        "payeeId": 1,
        "isEmailSent": true,
        "isSmssent": true,
        "voucherNumber": null,
        "vendorInvoiceStatusId": null,
        "postedBy": parseInt(this.cookieService.get('userId')),
        "postedOn": new Date().toISOString(),
        "comments": this.invoice.comments || "",
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString(),
        "transReference1": "string",
        "transReference2": "string",
        "vendorInvoiceGlaccount": [
          {
            "vendorInvoiceGlaccountId": this.invoice.vendorInvoiceGlaccountId,
            "apartmentId": parseInt(this.cookieService.get('apartmentId')),
            "invoiceId": 0,
            "glaccountId": parseInt(this.invoice.glaccountId),
            "comments": this.invoice.comments || "",
            "isActive": true,
            "insertedBy": parseInt(this.cookieService.get('userId')),
            "insertedOn": new Date().toISOString(),
            "updatedBy": null,
            "updatedOn": new Date().toISOString(),
            "tax1": 0,
            "tax2": 0,
            "tax3": 0,
            "deductTax": 0,
            "amount": this.invoice.vendorInvoiceAmount
          }
        ]

      }

      this.accountsService.updateVendorInvoice(details).subscribe((res:any) => {

        if(res.message){

          this.isInvoiceSubmitted = true;
          this.sharedService.setAlertMessage("Expense updated successfully");
          this.router.navigate(['ams/expense/subledger']);

        }
        else {

          this.isInvoiceSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
           
        }

      },
      error => {

        this.isInvoiceSubmitted = true;

      });


    }

  }

  ngOnInit() {

  	this.invoice = {};
  	this.invoice.glaccountId = "";
  	this.invoice.vendorId = this.route.params['value'].id;

    this.vendorService.getVendorByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.vendorId == this.route.params['value'].id;
      });

      this.isVendorDataLoaded = true;

      this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {

        this.vendorCategoryData = res.filter(item => {
            return item.isActive;
        });

      });


  });

  	if(this.route.params['value'].id != undefined && this.route.params['value'].type == undefined){

  		this.isEditInvoice = true;

      this.accountsService.getVendorInvoiceByVendorId(this.route.params['value'].id).subscribe((res:any) => {

        console.log(res);

        /*this.invoice = res[0];
        this.invoice.vendorInvoiceGlaccountId = this.invoice.vendorInvoiceGlaccount[0].vendorInvoiceGlaccountId;
        this.invoice.glaccountId = this.invoice.vendorInvoiceGlaccount[0].glaccountId;

        this.invoice = {};*/

      });

  	}
  	else {

      this.isNewInvoice = true;

      this.vendorService.getVendorById(this.route.params['value'].id).subscribe((res:any) => {

        this.invoice.vendorTypeId = res[0].vendorTypeId;
        this.invoice.vendorName = res[0].vendorName;

      });

    }

    this.accountsService.getAllGLAccounts().subscribe((res:any) => {

      this.glAccountListData = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
      });

    });

  }

}
