import { Component, OnInit, Injector, ViewChild, TemplateRef } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { ApartmentService } from '../../../../api/services/apartment.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { VendorService } from '../../../../api/services/vendor.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-expense-sub-ledger',
  templateUrl: './expense-sub-ledger.component.html',
  styleUrls: ['./expense-sub-ledger.component.scss']
})
export class ExpenseSubLedgerComponent implements OnInit {
  
  isSubLedgerDataLoaded:boolean = false;

  subLedgerData:string = "";
  subLedgerDataList:any;

  vendorDataList:any;
  vendorCategoryData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "vendorId";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  totalDueAmount:number = 0;
  netDueAmount:number = 0;

  blockListData:any;

  filterSelected:string = "all";
  singleBlock:string = "Select Block";
  selectedBlockId:number;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  dialogRef:any;

  selectedInput:string = "";
  columnField: any = {}

  @ViewChild("viewInvoiceTypeRef", { static: false }) viewInvoiceTypeRef: TemplateRef<any>;

  constructor(
  	private injector: Injector,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private vendorService:VendorService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getDueAmount(value){
  	return value > 0 ? 'high' : 'low';
  }

  getSelected(type){
  	return this.filterSelected == type ? 'selected' : '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  clearFilter(){
  	this.filterSelected = 'all';
  	this.singleBlock = "Select Block";
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  getVendorCategory(ledger, id){

    var data = _.filter( this.vendorDataList, function( item ) {
      if(item.vendorId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      
      var categoryId = data[0].vendorCategoryId;

      var name = _.filter( this.vendorCategoryData, function( item ) {
        if(item.lookupValueId === categoryId)
          return item;
      });

      if(name === undefined || name.length == 0){
        return '';
      }
      else {

        this.subLedgerDataList.map(obj=> {
          if(obj.vendorId == ledger.vendorId){
            obj.vendorCategory = name[0].lookupValueName.toLowerCase();
          }
        });

        return name[0].lookupValueName;
      }

    }

  }

  showInvoiceTypes(ledger){

    this.dialogRef = this.dialog.open(this.viewInvoiceTypeRef, {
        panelClass: 'material',
        disableClose: true,
        data: ledger
    });

  }

  dialogClose(){
    this.dialogRef.close();
  }
 
  ngOnInit() {

    this.accountsService.GetExpenseTrackerInvoicesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.subLedgerDataList = res;

      this.totalItems = this.subLedgerDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.netDueAmount = 0;
      this.totalDueAmount = 0;

    _.each(this.subLedgerDataList, (item, index) => {

      this.netDueAmount = this.netDueAmount + item.due;

      if(item.due >= 0)
      this.totalDueAmount = this.totalDueAmount + item.due;

    });

    this.lookupService.getLookupValueByLookupTypeId(57).subscribe((res:any) => {
        
     this.vendorCategoryData = res.filter( item => {
        return item.isActive;
     });


     this.vendorService.getVendorByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.vendorDataList = res;
      this.isSubLedgerDataLoaded = true;

    });

    },
    error => {

    });

    });

  }

}
