import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { ExpenseTrackerComponent } from './expense-tracker.component';
import { ExpenseSubLedgerComponent } from './components/expense-sub-ledger/expense-sub-ledger.component';
import { ExpenseViewInvoiceComponent } from './components/expense-view-invoice/expense-view-invoice.component';
import { ExpensePostInvoiceComponent } from './components/expense-post-invoice/expense-post-invoice.component';
import { ExpensePostPaymentComponent } from './components/expense-post-payment/expense-post-payment.component';
import { ExpenseViewInvoiceHistoryComponent } from './components/expense-view-invoice-history/expense-view-invoice-history.component';
import { ExpensePayInvoiceComponent } from './components/expense-pay-invoice/expense-pay-invoice.component';
import { ExpensePostMultiInvoiceComponent } from './components/expense-post-multi-invoice/expense-post-multi-invoice.component';
import { ExpensePostMultiInvoiceFieldsComponent } from './components/expense-post-multi-invoice-fields/expense-post-multi-invoice-fields.component';
import { ExpenseTrackerReportsComponent } from './components/expense-tracker-reports/expense-tracker-reports.component';


const routes: Routes = [
	{ path: '', redirectTo: 'subledger', pathMatch: 'full'},
	{ path: 'subledger', component: ExpenseSubLedgerComponent},
	{ path: 'view-invoice-history/:id', component: ExpenseViewInvoiceHistoryComponent},
	{ path: 'all-invoices', component: ExpenseViewInvoiceComponent},
	{ path: 'post-invoice/:id/:type', component: ExpensePostInvoiceComponent},
	{ path: 'post-multi-invoice/:id/:type', component: ExpensePostMultiInvoiceComponent},
	{ path: 'edit-invoice/:id', component: ExpensePostInvoiceComponent},
	{ path: 'post-payment/:id', component: ExpensePayInvoiceComponent},
	{ path: 'reports', component: ExpenseTrackerReportsComponent},
	{ path: '**', redirectTo: 'subledger', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ExpenseTrackerRoutingModule { }
