import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, ChangeDetectorRef, ViewChild } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ControlContainer, NgForm, FormsModule } from '@angular/forms';

import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-expense-post-multi-invoice-fields',
  templateUrl: './expense-post-multi-invoice-fields.component.html',
  styleUrls: ['./expense-post-multi-invoice-fields.component.scss']
})
export class ExpensePostMultiInvoiceFieldsComponent implements OnInit {

  invoiceId:string = "";

  isEditInvoice:boolean = false;

  glAccountListData:any;

  isInvoiceSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  invoiceGLAccountsData:any;

  glAccountIndicator:any = "Expenses";

  @Input () index;
  @Input () invoiceGLAccountsArray;

  @Output() public fieldParams = new EventEmitter();

  @ViewChild('postExpenseMultiInvoiceFieldsForm', {static : true}) public invoiceForm: NgForm;

 constructor(
    private cd: ChangeDetectorRef,
  	private router: Router,
    private route: ActivatedRoute,
    private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }


  deleteBlock(){
    this.invoiceGLAccountsArray.splice(this.index, 1);
    this.fieldParams.emit(this.invoiceGLAccountsArray);
  }

  isArrayOne(){
    return this.invoiceGLAccountsArray.length > 1 ? true : false;
  }


  onAccountChange(id){
    this.invoiceGLAccountsArray[this.index].glaccountId = parseInt(id);
    this.invoiceGLAccountsArray[this.index].form = this.invoiceForm.valid;
    this.fieldParams.emit(this.invoiceGLAccountsArray);
  }

  onInvoiceAmountChange(value){
    this.invoiceGLAccountsArray[this.index].amount = parseInt(value);
    this.invoiceGLAccountsArray[this.index].form = this.invoiceForm.valid;
    this.fieldParams.emit(this.invoiceGLAccountsArray);
  }

  onCommentChange(value){
    this.invoiceGLAccountsArray[this.index].comments = value;
    this.invoiceGLAccountsArray[this.index].form = this.invoiceForm.valid;
    this.fieldParams.emit(this.invoiceGLAccountsArray);
  }

  ngOnInit() {

    this.invoiceGLAccountsData = this.invoiceGLAccountsArray[this.index];

    this.accountsService.getAllGLAccounts().subscribe((res:any) => {

      this.glAccountListData = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
      });

    });

  }

  ngOnChanges(changes: SimpleChanges) {

    this.invoiceGLAccountsData = this.invoiceGLAccountsArray[this.index];
    console.log(this.invoiceGLAccountsArray);

  }

}
